<template>
  <v-app>
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Register</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-alert type="error" dark v-if="hasError">
                  {{errorMessage}}
                </v-alert>
                <v-form>
                  <v-text-field v-model="form.name" :disabled="loading" prepend-icon="mdi-account" label="Name" type="text"></v-text-field>
                  <v-text-field v-model="form.email" :disabled="loading" prepend-icon="mdi-email" label="Email" type="text"></v-text-field>
                  <v-text-field v-model="form.password" :disabled="loading" prepend-icon="mdi-lock" label="Password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" block @click="submit">Register</v-btn>
              </v-card-actions>
              <v-divider></v-divider>
              <div style="padding: 10px;">
                Already have an account? <router-link to="/login">Log in</router-link>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      hasError: false,
      errorMessage: null,
      loading: false,
      form: {
        name: null,
        email: null,
        password: null
      }
    }
  },
  methods: {
    async submit() {
      this.loading = true;
      this.hasError = false;
      try {
        let response = await axios.post('/api/auth/signup', this.form);
        this.$store.commit('login', response.data);
        this.$router.push('/');
      } catch (e) {
        if (e.response && e.response.data && e.response.data.error && e.response.data.error.details) {
          this.errorMessage = e.response.data.error.details[0].message;
        } else if (e.response && e.response.data && e.response.data.error && e.response.data.error.message) {
          this.errorMessage = e.response.data.error.message;
        } else {
          this.errorMessage = 'An error occurred!';
        }
        this.loading = false;
        this.hasError = true;
      }
    }
  }
}
</script>